import React from 'react';
import logo from '../../logo.svg'
// import logo from '../../images/logo.png'
import '../../index.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import {Navbar,Nav,NavLink,Image} from 'react-bootstrap'

function NavBar() { 
    return(
        <Navbar collapseOnSelect bg="light" expand="md" variant="light">
                <Navbar.Brand className="text-center" ><NavLink to={'/'}><Image src={logo} alt="Logo" width="50px;" /><span className="logoFont brownColor m-2" style={{color:'#603913'}}>Kalpa Coconuts</span></NavLink> </Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                {/* <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="mr-auto"></Nav>

                </Navbar.Collapse> */}
            </Navbar>
    )
}
export default NavBar;
